import React, { useState } from 'react';
import { useFile } from '../../context/FileContext';
import { useAuth } from '../../context/AuthContext';
import { DocumentPlusIcon, FolderPlusIcon, ArrowDownTrayIcon, FolderOpenIcon, FolderIcon, ChevronDownIcon, ChevronRightIcon } from '@heroicons/react/24/outline';
import { File as PersistentFile } from '../../services/fileService';
import { getLanguageIcon } from '../../utils/language-icons';

// Define FileSystemItem interface locally to match the one in FileContext
interface FileSystemItem {
    id: string;
    name: string;
    type: 'file' | 'directory';
    parentId: string | null;
    content?: string;
    language?: string;
    isOpen?: boolean;
    children?: FileSystemItem[];
}

interface FileTreeItemProps {
    item: FileSystemItem;
    level: number;
    onOpenFile: (file: FileSystemItem) => void;
    onToggleDirectory: (dirId: string) => void;
    onCreateFile: (parentId: string | null) => void;
    onCreateFolder: (parentId: string | null) => void;
}

const FilePanel: React.FC = () => {
    const { 
        fileStructure, 
        createFileSystemFile, 
        createDirectory, 
        openFile, 
        toggleDirectory, 
        downloadFilesAndFolders 
    } = useFile();

    const [creatingType, setCreatingType] = useState<'file' | 'folder' | null>(null);
    const [newItemName, setNewItemName] = useState('');
    const [selectedParent, setSelectedParent] = useState<string | null>('root'); // 'root' or an item ID

    const handleStartCreating = (type: 'file' | 'folder', parentId: string | null = 'root') => {
        setCreatingType(type);
        setSelectedParent(parentId);
        setNewItemName('');
    };
    
    const handleCancelCreating = () => {
        setCreatingType(null);
        setNewItemName('');
    };

    const handleConfirmCreating = () => {
        if (newItemName.trim() === '') return;

        if (creatingType === 'file') {
            createFileSystemFile(selectedParent || '', newItemName);
        } else if (creatingType === 'folder') {
            createDirectory(selectedParent || 'root', newItemName);
        }
        handleCancelCreating();
    };
    
    const renderInput = (parentId: string | null, level: number) => {
        if (creatingType && selectedParent === parentId) {
            return (
                <div style={{ paddingLeft: `${level * 1.5}rem` }} className="py-1">
                    <input
                        type="text"
                        placeholder={creatingType === 'file' ? 'New file name...' : 'New folder name...'}
                        value={newItemName}
                        onChange={(e) => setNewItemName(e.target.value)}
                        onKeyDown={(e) => {
                            if (e.key === 'Enter') handleConfirmCreating();
                            if (e.key === 'Escape') handleCancelCreating();
                        }}
                        className="w-full bg-gray-800 border border-gray-700 rounded px-2 py-1 text-xs focus:outline-none focus:ring-1 focus:ring-blue-500"
                        autoFocus
                        onBlur={handleCancelCreating} // Cancel on blur
                    />
                </div>
            );
        }
        return null;
    };

    const handleCreateFile = (parentId: string | null) => {
        handleStartCreating('file', parentId);
    };

    const handleCreateFolder = (parentId: string | null) => {
        handleStartCreating('folder', parentId);
    };

    const FileTreeItem: React.FC<FileTreeItemProps> = ({ item, level, onOpenFile, onToggleDirectory, onCreateFile, onCreateFolder }) => {
        const { abbr, color } = getLanguageIcon(item.name);

        const handleContextMenu = (e: React.MouseEvent) => {
            e.preventDefault();
            // For now, just create at root level. In a full implementation, you'd show a context menu
            if (item.type === 'directory') {
                onCreateFolder(item.id);
            }
        };

        if (item.type === 'directory') {
            return (
                <div>
                    <div 
                        className="flex items-center px-4 py-1.5 cursor-pointer text-sm hover:bg-gray-700"
                        style={{ paddingLeft: `${level * 1.5}rem` }}
                        onClick={() => onToggleDirectory(item.id)}
                        onContextMenu={handleContextMenu}
                    >
                        {item.isOpen ? <ChevronDownIcon className="h-4 w-4 mr-1" /> : <ChevronRightIcon className="h-4 w-4 mr-1" />}
                        <FolderIcon className="h-4 w-4 mr-2 text-yellow-400" />
                        <span>{item.name}</span>
                    </div>
                    {item.isOpen && (
                        <>
                            {item.children?.map(child => (
                                <FileTreeItem 
                                    key={child.id} 
                                    item={child} 
                                    level={level + 1} 
                                    onOpenFile={onOpenFile}
                                    onToggleDirectory={onToggleDirectory}
                                    onCreateFile={onCreateFile}
                                    onCreateFolder={onCreateFolder}
                                />
                            ))}
                            {renderInput(item.id, level + 1)}
                        </>
                    )}
                </div>
            );
        }

        return (
            <div
                className={`flex items-center px-4 py-1.5 cursor-pointer text-sm hover:bg-gray-700`}
                style={{ paddingLeft: `${level * 1.5}rem` }}
                onClick={() => onOpenFile(item)}
            >
                <span className={`${color} w-6 text-center mr-2 font-mono text-xs`}>{abbr}</span>
                <span>{item.name}</span>
            </div>
        );
    };

    return (
        <div className="flex flex-col h-full bg-[#1e1e1e] text-gray-300">
            <div className="flex items-center justify-between p-2 border-b border-gray-700">
                <h2 className="text-sm font-medium px-2">Files</h2>
                <div className="flex items-center space-x-1">
                    <button onClick={() => handleStartCreating('file', 'root')} className="p-1 hover:bg-gray-700 rounded" title="New File">
                        <DocumentPlusIcon className="h-4 w-4" />
                    </button>
                    <button onClick={() => handleStartCreating('folder', 'root')} className="p-1 hover:bg-gray-700 rounded" title="New Folder">
                        <FolderPlusIcon className="h-4 w-4" />
                    </button>
                </div>
            </div>

            <div className="flex-1 overflow-y-auto">
                {fileStructure.map(item => (
                    <FileTreeItem 
                        key={item.id} 
                        item={item} 
                        level={1}
                        onOpenFile={openFile}
                        onToggleDirectory={toggleDirectory}
                        onCreateFile={handleCreateFile}
                        onCreateFolder={handleCreateFolder}
                    />
                ))}
                {renderInput('root', 1)}
            </div>

            <div className="p-2 border-t border-gray-700">
                <button onClick={downloadFilesAndFolders} className="flex items-center w-full text-left px-2 py-1.5 text-xs hover:bg-gray-700 rounded mt-1">
                    <ArrowDownTrayIcon className="h-4 w-4 mr-2" />
                    Download Project
                </button>
            </div>
        </div>
    );
};

export default FilePanel;